@tailwind base;
@tailwind components;
@tailwind utilities;
/* ----------------------------------- */
@font-face {
    font-family: 'Rubik';
    src: local('Rubik'), url('./assets/fonts/Rubik-VariableFont_wght.ttf') format('woff');
}

html {
    background-color: #f9fafb;
    font-family: "Rubik", sans-serif;
    scroll-behavior: smooth;
    overflow-x: hidden;
}

:root {
    --background: #F0F0F0;
    --black: #252525;
    --hexa-orange: #FD5301;
    --hexa-orange-lighter: rgb(226, 127, 77);
    --hexa-orange-darker: #d74800;
    --description: #5E5E5E;
    --inputs: #d7d7d7;
    --purple: #4f46e5;
    --purple-hover: #4338ca;
    --table-tiles: #eef2ff;
}
.bg-purple-alt {
    border: 2px solid var(--purple);
    color: var(--black);
}
.text-purple {
    color: var(--purple);
}
.bg-table-titles {
    background-color: var(--table-tiles);
}
.bg-purple {
    background-color: var(--purple);
}
.bg-purple:hover {
    background-color: var(--purple-hover);
}
.bg-primary-color {
    background-color: var(--primary-color);
}
.bg-hexa-orange-lighter {
    background-color: var(--hexa-orange-lighter);
}
/* -----Container-bullet-items-center-------- */
.container-bullet-items {
    display: grid; 
    place-content: center;
}
.container-bullet-items-alt {
    display: flex;
    place-content: center;
}
.container-bullet-items-alt div {
    display: flex;
    justify-content: left;
    align-items: center;
    width: 300px;
    margin-top: 30px;
}
/* ----------------------------------- */
.h-max-content {
    height: max-content;
}
.w-max-content {
    width: max-content;
}
.dividerLine {
    height: 0.25px;
    width: 90%;
    margin: 30px auto;
    background-color: #d2d2d2;
}
.noSelection {
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -o-user-select: none;
    user-select: none;
}
/* --------------Textos-------------- */
.h1 {
    font-size: 48px;
    font-family: 'Intern-semibold';
}
.h2 {
    font-size: 40px;
    font-family: 'Intern-semibold';
}
.h3 {
    font-size: 32px;
    font-family: 'Intern-semibold';
}
.description {
    color: var(--description);
    margin: 10px auto 0 auto;
    font-size: 1.125rem;
    line-height: 1.75rem;
}
/* ----------------------------------- */
.data-container {
    margin-left: 0;
    width: 100vw;
}
.footer-container {
    margin-left: 0;
    width: 100vw;
}
.data-container-child {
    margin-left: 0;
    width: 100vw;
}
.buttons-pannel {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    width: 100%;
}
/* ----------------------------------- */
::-webkit-scrollbar {
    width: 8px;
    height: 8px
}

::-webkit-scrollbar-track {
    background: #dce2f5;
}

::-webkit-scrollbar-thumb {
    background: #7471b6;
    border-radius: 2rem;
}

/* ----------------------------------- */
@media (min-width: 1025px) {
    .data-container {
        /* padding: 100px; */
        margin-left: 191px;
        padding-top: 64px;
        width: calc(100vw - 191px);
    }
    .footer-container {
        margin-left: 191px;
        width: calc(100vw - 191px);
    }
    .data-container-child {
        /* padding: 100px; */
        margin-left: 20px;
        width: calc(100vw - 230px);
    }
    .buttons-pannel {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 20px;
        width: calc(100vw - 191px);
    }
}

.labelRadio {
    color: #4f46e5;
    border: 2px solid #4f46e5;
}

input[type="radio"]:checked+label {
    background-color: #4f46e5;
    color: white;
}

input[type="checkbox"]:checked+label {
    background-color: #4f46e5;
    color: white;
}

.grow {
    transition: all 0.2s ease-in-out;
}

.grow:hover {
    transform: scale(1.05);
}

.width-table {
    width: calc(100% - 32px);
}

@media(min-width: 1024px) {
    .width-table {
        width: 100%;
    }
}